import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'
import { Paper } from '@material-ui/core'

import API from '../api/Api'

import { NavLink, withRouter } from 'react-router-dom'
import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'
import MaterialTable from 'material-table'


const OrgAccount = ({ history }) => {

  const columns = [
    {
      field: 'name',
      title: ''
    },
    {
      field: 'hp',
      title: ''
    },
    {
      field: 'p_code',
      title: 'pcode'
    },
    {
      field: 'entry_dt',
      title: ''
    },
    // {
    //   field: 'idk',
    //   title: ''
    // },
    // {
    //   field: 'disp_class',
    //   title: '  '
    // }
  ]

  const init = async (page = 0, pageSize = 100) => {
    const { totalCount = 0, items = [] } = await API.getAccounts('member', { page, pageSize })

    const data = items.map(item => {
      item.entry_dt = format(new Date(item.entry_dt), 'yyyy-MM-dd HH:mm:dd')
      return item
    })
    
    return {
      data,
      page,
      totalCount
    }
  }

  useEffect(() => {
    init()
  }, [])
  
  const handleRowClick = (e, rowData) => {
    history.push(`/member/account/view/${rowData.uuid}`)
    
    return false
  }

  return (
    <>
      <DefaultTemplate>
        <MaterialTable
          title="  "
          columns={columns}
          data={query =>
            new Promise((res, rej) => {
              init(query.page, query.pageSize)
                .then(result => {
                  res(result)
                })
            })
          }
          onRowClick={handleRowClick}
          options={{
            draggable: false,
            search: false,
            doubleHorizontalScroll: true,
            columnsButton: true,
            filtering: false,
            exportButton: true,
            pageSize: 100,
            pageSizeOptions: [100, 50, 20],
          }}
          components={{
            Container: props => (
              <Paper {...props} className="custom_table" />
            )
          }}
          localization={{
            body: {
              emptyDataSourceMessage: '  .',
              filterRow: {
                filterTooltip: ''
              },
              editRow: {
                saveTooltip: '',
                cancelTooltip: '',
                deleteText: '   ?'
              },
              addTooltip: '',
              deleteTooltip: '',
              editTooltip: ''
            },
            grouping: {
              placeholder: '   '
            },
            toolbar: {
              searchTooltip: '',
              searchPlaceholder: '',
              exportTitle: ' ',
              exportName: 'CSV ',
              showColumnsTitle: '',
              addRemoveColumns: '   '
            },
            pagination: {
              labelRowsSelect: ``,
              firstTooltip: ' ',
              previousTooltip: ' ',
              nextTooltip: ' ',
              lastTooltip: ' ',
              labelDisplayedRows: `{count}  {from}~{to}`
            }
          }}
        />
      </DefaultTemplate>
    </>
  )
}

export default withRouter(OrgAccount)