import React, { useEffect } from 'react'
import { format } from 'date-fns'

import '../components/queryTrace/queryTrace.scss'
import API from '../api/Api'

import Table from '../components/table/Table'
import DefaultTemplate from '../components/template/DefaultTemplate'


const PostingLookup = () => {
  const [data, setData] = React.useState([])

  const columns = [
    {
      field: 'issue_datetime',
      title: '',
      render: rowData => format(new Date(rowData.issue_datetime), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'issue_number',
      title: ''
    },
    {
      field: 'from_name',
      title: '-',
    },
    {
      field: 'from_license_num',
      title: '-()'
    },
    {
      field: 'to_name',
      title: '-'
    },
    {
      field: 'to_pi',
      title: '-PI'
    },
    {
      field: 'send_date',
      title: ''
    },
    {
      field: 'recv_date',
      title: '',
    },
    {
      field: 'read_date',
      title: ''
    },
    {
      field: 'doc_title',
      title: ''
    },
    {
      field: 'doc_hash',
      title: ''
    },
    {
      field: 'issue_date',
      title: ' ()'
    }
  ]

  const init = async () => {
    const res = await API.getCertHistory()

    setData(res)
  }

  useEffect(() => {
    init()
  }, [])


  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="issue_datetime"
        title="   "
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingLookup